<template>
   <div id="Landing">

        <v-card class="elevation-12">
            
            <v-card-title v-if="!errorText">
                <v-icon class="mr-2">{{headIcon}}</v-icon>
                <small class="text-left">{{headText}}</small>        
                <small class="text-right ml-1">{{step}}/3</small>
            </v-card-title>

            <v-card-title v-else-if="errorText">
                <v-icon class="mr-2" dense>mdi-alert</v-icon>
                <small class="text-left">{{errorText}}</small>   
            </v-card-title>

        </v-card>
        
   </div>
</template>

<script>

    import CodeInput from "vue-verification-code-input"

    export default {
        
        name: 'Landing',
        
        data () {
            return {
                step: 1,
                headText: '',
                headIcon: '',
                errorText: '',
                loading: false,
                serviceCodeVerified: false
            }
        },

        computed: {
            serviceCode () {
                return this.$route.params.serviceCode;
            }
        },

        methods: {
            
            start () {
                    
                this.step = 0
                this.headText = 'Carregando'
                this.headIcon = 'mdi-refresh'
                
                setTimeout(() => {
                    
                    this.step = 1
                    this.headText = 'Validando de serviceCode'
                    this.headIcon = 'mdi-refresh'

                    this.validateServiceCodeFormat(() => {

                        this.step = 2
                        this.headText = 'Verificando serviceCode'
                        this.headIcon = 'mdi-refresh'

                        this.verifyServiceCode(() => {
                            this.step = 3
                            this.headText = 'Iniciando serviço'
                            this.headIcon = 'mdi-refresh'
                            this.setCookiesAndPush()
                        })
                    })

                }, 2500)
            },

            setCookiesAndPush () {
                setTimeout(() => {
                    window.localStorage.setItem('serviceCode', this.serviceCode)
                    window.localStorage.setItem('serviceMaxInfos', 150)
                    window.localStorage.setItem('serviceTeleNumber', '11 99345-3413')
                    window.localStorage.setItem('serviceTotalPrice', 'R$ 76,54')
                    this.$router.push('/check')
                }, 2500)
            },

            validateServiceCodeFormat (cb) {
                
                let err  = 'Não é numérico.',
                    err2 = 'Quantidade invalida de caracteres.';

                if (this.serviceCode.length === 6) {
                    if(!window.isNaN(this.serviceCode)) {
                        cb(true, null)
                    } else { 
                        this.errorText = err
                        console.error(err)
                    }
                } else { 
                    this.errorText = err2
                    console.error(err2)
                }
            },

            verifyServiceCode (cb) {
                setTimeout(() => {
                    cb(true, null)
                }, 2500)
            }
        },

        mounted () {
            this.start()
        },

        components: {
            CodeInput
        }
    }
</script>

