<template>
  <div id="Error">

      ERROR <b>{{errorCode}}</b>
      <p>{{ errorText || 'notext' }}</p>

  </div>
</template>

<script>
    
    export default {    
        
        name: 'Error',

        computed: {
            
            errorCode () {
                return Number(this.$route.params.errorCode)
            },

            errorText () {
                return this.$route.params.errorText;
            }
        }
    };

</script>

