import Vue from 'vue'
import VueRouter from 'vue-router'

import Landing from './components/views/Landing.vue'
import Check from './components/views/Check.vue'
import Thanks from './components/views/Thanks.vue'
import Error from './components/views/Error.vue'

Vue.use(VueRouter)

const routes = [
  
  {
    path: '/landing/:serviceCode',
    name: 'Landing',
    component: Landing,
    meta: { step: '1', icon: 'mdi-home'}
  },

  {
    path: '/check',
    name: 'Check',
    component: Check,
    meta: { step: '2', icon: 'mdi-credit-card-check'}
  },

  {
    path: '/thanks',
    name: 'Thanks',
    component: Thanks,
    meta: { step: '3', icon: 'mdi-robot-happy-outline'}
  },

  {
    path: '/error/:errorCode/:errorText',
    name: 'Error',
    component: Error,
    meta: { icon: 'mdi-error'}
  },

  {
      path: '*',
      redirect: '/error/404/NOT_FOUND_ROUTE',
      meta: { icon: 'mdi-error'}
  }
]

const router = new VueRouter({
    routes
})

export default router
