function notify (
        _text, 
        _type    = 'success',
        _cb      =  () => {},
        _timeout = 1500 ) {
         // error, 
         // warning, 
         // info, 
         // success
      this.$toast[_type](_text, {
        position: "bottom-right",
        timeout: _timeout,
        closeOnClick: false,
        pauseOnFocusLoss: false,
        pauseOnHover: false,
        draggable: true,
        draggablePercent: 1,
        showCloseButtonOnHover: false,
        hideProgressBar: false,
        closeButton: "button",
        icon: true,
        rtl: false,
        onClose: _cb || (() => {})
    })    
}

export default notify;