<template>
    <div id="Check">
        
        <v-row>
                    
            <v-col 
                v-if="step === 1"
                cols="12" 
                md="5"
                ref="cardInputCol">

                    <p class="text-left" style="font-size: 95% !important'">
                        serviço: #{{getls('serviceCode')}} 
                        telenum: {{getls('serviceTeleNumber')}} 
                        <br>
                        max infos: {{getls('serviceMaxInfos')}} 
                        valor total: {{getls('serviceTotalPrice')}}
                    </p>
                    
                    <v-card
                        id="cardInput"
                        class="elevation-3 pb-0 mt-1" 
                        :loading="loading"
                        style="border: 1px solid rgba(0,0,0,0.2)">

                        <v-card-subtitle class="white--text">
                            <v-row>
                                <v-col cols="5" md="5" class="pb-0 pb-3 pr-4">
                                    <v-btn
                                        :loading="loading"
                                        color="primary"
                                        class="white--text"
                                        @click="checkInfos">
                                        iniciar
                                        <v-icon dense class="ml-3">
                                            mdi-motion-play
                                        </v-icon>
                                    </v-btn>
                                </v-col>
                                <v-col cols="7" md="7" class="black--text">
                                    <b>{{step}}/2</b>
                                </v-col>
                            </v-row>
                        </v-card-subtitle>


                        <v-textarea 
                            outlined
                            autofocus
                            v-model="ccRawTextList" 
                            :loading="loading"
                            label="Lista de infos"
                            :placeholder="textareaPlaceholder"
                            rows="13"
                            class="pa-2 mt-2"
                            style="line-height: 1rem !important; font-size: 75% !important;"
                            :hint="`${ccRawTextArray.length} de 100 (por turno)`"
                            persistent-hint
                            :error="ccRawTextArray.length > 100"
                            :error-messages="
                                    ccRawTextArray.length > 100 
                                        ? [`${ccRawTextArray.length} de 100 (por turno)`] 
                                        : []" />

                      
                    </v-card>
                </v-col>

                <v-col 
                    v-if="step === 2"
                    cols="12" 
                    md="7"
                    ref="cardResultCol">
                    
                    <v-card 
                        id="cardResult"
                        class="elevation-3" 
                        :loading="loading"
                        style="border: 1px solid rgba(0,0,0,0.2)">
                        
                        <v-card-subtitle 
                            class="elevation-1 white--text" 
                            style="background-color: #363636">

                            <v-row>
                                
                                <v-col 
                                    md="6"
                                    class="pa-0 pl-3">
                                    <v-checkbox 
                                        small
                                        dark
                                        disabled
                                        v-model="includeInvalids"
                                        label="não incluir inválidas" />
                                </v-col>

                                <v-col md="3" class="text-right">
                                    <v-btn
                                        :loading="loading" 
                                        small 
                                        width="10px"
                                        class="white--textmr-4" 
                                        color="primary"
                                        v-on:mousedown="startBinCopy"
                                        v-on:mouseup="waitingBinCopy = false"
                                        @dblclick="infoToClipboard(f$infoObjectList)">
                                        <v-icon small>mdi-content-copy</v-icon>
                                    </v-btn>
                                </v-col>

                                <v-col md="2" class="text-right">
                                    <v-btn
                                        :loading="loading" 
                                        small 
                                        class="white--text" 
                                        color="green"
                                        @click="storeInfos">
                                        pronto
                                        <v-icon small class="ml-3">mdi-check-circle</v-icon>
                                    </v-btn>

                                </v-col>
                            </v-row>

                        </v-card-subtitle>
                        
                        <hr>

                        <v-card-text class="pt-0">
                            
                            <v-list v-if="f$infoObjectList[0]">

                                <div style="font-size: 105%; text-align: right; font-style: italic">
                                    <span class="mr-2">Total: <b>{{checkingStats.total}}</b></span>
                                    <span class="mr-2">Válidos: <b>{{checkingStats.valid}}</b></span>
                                    <span class="mr-2">Inválidos: <b>{{checkingStats.invalid}}</b></span>
                                    <span class="mr-2">Crédito: <b>{{checkingStats.credit}}</b></span>
                                    <span class="mr-2">Débito: <b>{{checkingStats.debit}}</b></span>
                                </div>

                                <hr class="mb-3" style="opacity: 0.3">
                                
                                <v-list-item
                                    v-for="(info, index) in f$infoObjectList"
                                    v-if="!info._errorValid"
                                    :key="'#' + index">
                                      
                                    <v-list-item-content>
                                        
                                        <div class="ellipsis-cut">
                                            <small 
                                                class="mr-1"
                                                style="color: green">
                                                {{index}}# 
                                            </small> 
                                            {{ infoObjToText(info) }}
                                        </div>

                                    </v-list-item-content>
                                </v-list-item>

                                <hr class="mt-3 mb-3">
                                
                                <v-list-item
                                    v-for="(info, index) in f$infoObjectList"
                                    v-if="info._errorValid"
                                    :key="'#' + index">
                                      
                                      <v-list-item-content class="ellipsis-cut">
   
                                        <div class="ellipsis-cut">
                                            <small 
                                                class="mr-1"
                                                style="color: red">
                                                {{index}}# 
                                            </small> 
                                            {{ infoObjToText(info) }}
                                        </div>

                                      </v-list-item-content>
                                </v-list-item>

                            </v-list>

                            <p v-else class="mt-5">
                                Nenhuma info importada ainda...
                            </p>

                        </v-card-text>
                        
                    </v-card>
                </v-col>
        </v-row>

    </div>
</template>

<script>

    // configs and pushes
    // ... a new service
    export default {
        
        name: 'Check',
        
        data () {

            return {
                step: 1,
                ///////
                waitingBinCopy: false,
                ////////////////////
                loading: false,
                humanVerified: false,
                includeInvalids: true,
                //////////////////
                ccRawTextList: '',
                ccObjectList: [],
                infoObjectList: [],
                ///////////////////
                textareaPlaceholder: 
                      '...exemplo \n'
                    + '5555532304440007|03|06|103\n'
                    + '5555532304440005|11|03|143\n'
                    + '5555532304440004|09|16|403\n'
                    + '5555532304440003|04|26|343\n'
            }
        },

        computed: {

            ccRawTextArray () {

                let _text = this.ccRawTextList;
                    _text = _text.replaceAll(' ', '');

                return _text.split('\n').filter(_txt => _txt !== '');
            },

            f$infoObjectList () {
                
                if (this.includeInvalids) return this.infoObjectList
                
                return this.infoObjectList.filter(_info => {
                    return !_info._errorValid
                })
            },

            checkingStats () {
                
                let _iol = this.f$infoObjectList // shortcut
                
                return {
                    total: _iol.length,
                    valid: _iol.filter(_info => !_info._errorValid).length,
                    invalid: _iol.filter(_info => _info._errorValid).length,
                    credit: _iol.filter(_info => _info.type === 'CREDIT').length,
                    debit: _iol.filter(_info => _info.type === 'DEBIT').length
                }
            }
        },

        methods: {

            setCcObjectList () {

                let _ll = this.ccRawTextArray.length

                if (_ll <= 100 && _ll !== 0) {
                    this.ccObjectList = this.infoTextToObj(this.ccRawTextArray, true);
                    return true
                } else if (_ll === 0) {
                    this.notify('Lista vazia.', 'error')
                    return false
                } else {
                    this.notify('Não ultrapassar 100 items por turno.', 'error')
                    return false
                }
            },
            
            async checkInfos () {
                
                if (!this.setCcObjectList()) {
                    return;
                }

                if (this.myself.credits < this.ccObjectList.length) {
                    this.notify('Créditos insuficientes.', 'error')
                    return;
                }

                this.loading = true;

                this.$refs.cardResultCol.scrollIntoView({ 
                    behavior: 'smooth', 
                    block: 'end' 
                })

                this.notify('Processando...', 'info', () => {

                    // -> main
                    this.axios({
                        url: '/checkParseMany',
                        method: 'post',
                        data: this.ccRawTextArray
                    }).then(_res => {
                        
                        this.infoObjectList = _res.data;

                        this.notify('Checks prontos!', 'success', () => {
                            
                            // clean all
                            this.ccRawTextList = ''
                            this.ccObjectList = []
                            this.loading = false;
                            
                            // update myself
                            this.fetchAndSetMyself({
                                axios: this.axios, 
                                route: this.$route
                            })
                        })
                    }).catch((_err) => {
                        this.notify(
                            'Plano ou saldo expirado: ' +
                                _err.toString(), 'error')
                        this.loading = false;
                    })
                    // - main

                }, 1000 * 5)
            },

            storeInfos () {

                this.loading = true;

                this.axios({
                    url: '/addManyInfos',
                    method: 'post',
                    data: this.f$infoObjectList
                }).then(_res => {
                    
                    this.notify('Infos adicionadas!', 'success', () => {
                        this.infoObjectList = []
                        this.$router.push('/infos')
                    })

                }).catch(_err => {
                    this.notify(_err.toString(), 'error')
                    this.loading = false;
                })
            },

            startBinCopy () {
                
                this.waitingBinCopy = true
                
                setTimeout(() => {

                    if (this.waitingBinCopy) {
                        this.infoToClipboard(this.f$infoObjectList, true)
                    }

                    this.waitingBinCopy = false

                }, 1000)
            }
        },

        mounted () {
            
            if (!window.localStorage.getItem('serviceCode')) {
                this.$router.push('/error/500/No serviceCode stored.');
            }
        }
    }

</script>

<style>

    #Check .v-list {
        font-size: 70% !important;
    }

    #Check .v-list-item {
        min-height: 0 !important;
        padding: 0 !important;
    }

    #Check .v-list .v-list-item__content {
        padding: 0 !important;
    }
    
    #Check #cardInput {
        height: calc(99vh - 140px) !important;
    }

    #Check #cardInput textarea {
        position: relative;
        top: 10px;
        height: calc(99vh - 265px) !important;
    }

    #Check #cardResult {
        height: calc(99vh - 140px) !important;
    }

    #Check #cardResult .v-card__text {
        height: calc(99vh - 190px) !important;
        overflow-x: hidden;
    }

</style>